.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin: 5px;

}

.card p {
    color: #8b4513;
    width: 150px;
    text-align: center;
}

.card img {
    height: 170px;
    width: 150px;
}

.cardGrid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}